@import "./generic/reset";
@import "./settings/buttons";
@import "./settings/colors";
@import "./settings/fonts";
// @import './variables/'

* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  line-height: 3rem;
  margin-top: 6rem;
  margin-bottom: 15rem;
  // background-image: url("../rice_fields.jpg");
  background: linear-gradient(rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.45)),
    url("../rice_fields.jpg");
  background-repeat: no-repeat;
  // background-position: center;
  background-size: cover;
  font-size: 13px;
}

.title {
  // border: 1px solid black;
  color: white;
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 1rem;
}

.container {
  // border: 1px solid black;
  border-radius: 8px;
  width: 75%;
  margin: 0 auto;
  padding: 0 15px;
  background-color: #fff;
}

