.people_icon {
  margin-right: 0.3rem;
  padding-right: 0.5rem;
}

.selections {
  display: flex;

  .travelers_box {
    // border: 1px solid black;
    padding-left: 0.2rem;
    padding-right: 0.2rem;
  }

  &_ways,
  .travelers_box {
    margin-top: 0.5rem;
    text-align: center;
    width: 6.5rem;
    border-radius: 3px;
  }

  &_ways:hover,
  #travelers:hover {
    background-color: #eceeef;
    transition: all 0.35s ease-in;
  }

  select:hover,
  #travelers:hover {
    cursor: pointer;
  }

  &_all {
    // border: 1px solid black;
    margin-top: 0.5rem;
    display: flex;
    justify-content: space-between;

    &--inputs {
      line-height: 2rem;
      width: 100%;
    }
  }
}

.search_box {
  // border: 1px solid black;
  display: flex;
  justify-content: flex-end;
  padding-bottom: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;

  &--btn {
    background-color: #309c83;
    padding: 0.7em 2em 0.7em 2em;
    border-radius: 3px;
    border: none;

    .fa-arrow-right {
      padding-left: 0.7em;
    }
  }
}
